#Background{
    display: inline-block;
    background-color: #25af6f;
    width: 100%;
    height: 100%;
    text-align: center;
}

#main {
    display: inline-block;
    margin: 10px 0 0 10px;
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    background-color: rgb(38, 109, 202);
    text-align: center;
}

#Title{
    display: inline-block;
    height: 30px;
    text-align: center;
    margin: 35px 0 20px 0;
    display: inline-block;
    font-size: 35px;
    font-weight: 600;
    color: #169fd4;
    margin-top: 5px;
}

select{
    font-size: 22px;
    width: 80px;
    height: 30px;
}

#MainPanel{
    display: inline-block;
    background-color: #e0dcdc;
    width: 420px;
    height: calc(100% - 20px);
    margin: 10px 0 0 10px;
    border-radius: 5px;
    text-align: left;
}

#Status{
    display: inline-block;
    background-color: #c20000;
    width: 100px;
    height: 26px;
    text-align: center;
    margin: 2px 0 0 5px;
    transition: background-color 500ms;
}

#Info{
    width: calc(100% - 10px);
    margin-left: 5px;
    height: 120px;
    border: solid 1px #acacac;
    background-color: #dce6eb;
    font-size: 18px;
    font-weight: 600;
    color: #6d6d6d;
    text-align: left;
    box-sizing: border-box;
    padding: 3px;
    overflow: auto;
}

input{
    font-size: 20px;
    font-weight: 60;
    width: 100px;
    height: 30px;
}

#ResaltList{
    background-color: #ffffff;
    width: 100%;
    height: 1800px;
    margin-top: -1px;
}

#History{
    font-size: 16px;
    font-weight: 600;
    width: 100%;
    border-radius: 3px;
    border: solid 1px #d3d3d3;
    height: 26px;
    margin-top: 3px;
    padding-left: 10px;
    background-color: #f0f1f0;
    box-sizing: border-box;
    color: black;
    text-align: left;
}